import request from '@/utils/request'
export function addBlogType(params) {
  return request({
    url: '/blogType/addBlogType',
    method: 'get',
    params: params
  })
}

export function getBlogTypes() {
  return request({
    url: '/blogType/getBlogTypes',
    method: 'get',
  })
}

export function delBlogType(params) {
  return request({
    url: '/blogType/delBlogType',
    method: 'get',
    params: params
  })
}





