<template>
  <div>
    <!-- 发起blog弹窗 -->
    <el-dialog :visible.sync="show.createBlog" width="900px" title="发起Blog">
      <el-form label-width="10%">
        <el-form-item label="Blog名称">
          <el-input v-model.trim="v.ei.title"></el-input>
        </el-form-item>
        <el-form-item label="类型">
          <el-row>
            <el-col :span="8">
              <el-select v-model="v.ei.blogType">
                <el-option :value="t.id" :label="t.title" v-for="(t,ti) in list.blogTypes" :key="ti"></el-option>
              </el-select>
            </el-col>
            <el-col :span="1" :offset="1" v-if="rewardShow">赏金</el-col>
            <el-col :span="8" v-if="!show.blogRewardInput && rewardShow">
              <el-select v-model="v.ei.reward">
                <el-option value="10" label="10">10</el-option>
                <el-option value="20" label="20">20</el-option>
                <el-option value="50" label="50">50</el-option>
                <el-option value="100" label="100">100</el-option>
                <el-option value="1" label="自定义" @click.native="show.blogRewardInput=true">自定义</el-option>
              </el-select>
            </el-col>
            <el-col v-if="show.blogRewardInput && rewardShow" :span="7">
              <el-input v-model.trim="v.ei.reward" @change="dealReward()"></el-input>
            </el-col>
            <el-col v-if="show.blogRewardInput && rewardShow" :span="1">
              <i class="el-icon-check bloggou" @click="show.blogRewardInput=false"></i>
            </el-col>
            <el-col v-if="show.blogRewardInput && rewardShow" :span="1">
              <i class="el-icon-close bloggou" @click="show.blogRewardInput=false"></i>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item label="内容">
          <tinymce :width="770" v-model="v.ei.content" v-if="show.createBlog"></tinymce>
        </el-form-item>

        <el-form-item>
          <el-row :gutter="10">
            <el-col :span="5" :offset="7">
              <el-button type="primary" size="medium" @click="addNewBlog()">写好了</el-button>
            </el-col>
            <el-col :span="2" :offset="0">
              <el-button>算了吧</el-button>
            </el-col>
          </el-row>
        </el-form-item>
      </el-form>
    </el-dialog>

    <!-- 添加blogtype弹窗 -->
    <el-dialog :visible.sync="show.addBlogType" title="添加blog类型">

      <el-form>
        <el-form-item label="类型名称">
          <el-input v-model.trim="ed.addBlogType.title" style="width: 70%;"></el-input>
        </el-form-item>
        <el-form-item label="类型Logo">
          <el-row>
            <el-col :span="12">
              <el-input v-model.trim="ed.addBlogType.logo"></el-input>
            </el-col>

            <!-- 上传按钮 -->
            <el-col :span="2" :offset="1">
              <el-upload :action="dataUploadPath">
                <el-button icon="el-icon-upload">点击上传</el-button>
              </el-upload>
            </el-col>

          </el-row>
        </el-form-item>

      </el-form>

      <el-divider></el-divider>
      <el-row>
        <el-col :offset="9" :span="2">
          <el-button type="primary" @click="addBlogType('confirm')">确定</el-button>
        </el-col>

        <el-col :offset="1" :span="2">
          <el-button @click="addBlogType('cancel')">取消</el-button>
        </el-col>
      </el-row>
    </el-dialog>

    <flatGreyHead></flatGreyHead>

    <!-- 导航 -->
    <navBar cur="3"></navBar>

    <el-container style="height: 90%; border: 2px solid #eee">
      <el-header style="text-align: right; font-size: 32px; ">
        <!-- 几个大的筛选按钮 -->
        <el-row>
          <!-- 已有的blog类型 -->
          <el-col v-for="(bt,bti) in list.blogTypes" :key="bti" :span="1">
            <div @click="changeBlogType(bt) ">
            <el-avatar :size="bt.id==currentBlogType.id? 35:25" :src="bt.logo" :title="bt.title"></el-avatar>
            </div>
          </el-col>
          <!-- 添加类型按钮 -->
          <el-col :span="1" :offset="1">
            <el-button type="info" icon="el-icon-plus" circle @click="addBlogType('show')"></el-button>
          </el-col>

          <!-- 取消类型筛选 -->
          <el-col  :offset="2" :span="1">
          <el-button  size="mini" @click="currentBlogType={},reloadBlogs()">全部类型</el-button>
          </el-col>

        <!-- 发起blog -->
        <el-col :offset="2" :span="1">
          <el-button size="mini" @click="createBlog()">发起blog</el-button>
        </el-col>
        </el-row>

      
      </el-header>

      <el-main style="height:900px">
        <el-table :data="list.blogs">
          <!-- 头部图标 -->
          <el-table-column width="50">
            <template slot-scope="scope">
              <img src="../assets/images/钱袋.png" v-if="scope.row.blogType=='悬赏' || scope.row.blogType=='征集' " style="height:30px; width:30px">
            </template>
          </el-table-column>

          <!-- 标题 -->
          <el-table-column prop="title">
            <template slot-scope="scope">
              <i class="el-icon-time"></i>
              <router-link :to="'/cDetail?tp=blog&id='+scope.row.id" target="_blank">{{ scope.row.title }}</router-link>
            </template>
          </el-table-column>

          <!-- 发布者 -->
          <el-table-column prop="creatorNickname" width="100"></el-table-column>

          <!-- 阅读次数 -->
          <!-- <el-table-column prop="viewCount" width="50"></el-table-column> -->

          <!-- 收藏次数 -->
          <!-- <el-table-column prop="collCount" width="50"></el-table-column> -->

          <!-- 评论次数 -->
          <!-- <el-table-column prop="commentCount" width="50"></el-table-column> -->

          <!-- 点赞次数 -->
          <!-- <el-table-column prop="thumbCount" width="50"></el-table-column> -->

          <!-- <el-table-column prop="name" label="姓名" width="120">
            </el-table-column>
            <el-table-column prop="address" label="地址">
          </el-table-column>-->

          <!-- 编辑 -->
          <el-table-column width="100">
            <template slot-scope="scope">
              <i class="el-icon-edit" @click="editBlog(scope.row.id)"></i>
            </template>
          </el-table-column>

          <!-- 删除 -->
          <el-table-column width="100">
            <template slot-scope="scope">
              <i class="el-icon-delete" @click="delBlog(scope.row.id)"></i>
            </template>
          </el-table-column>
        </el-table>

        <el-pagination :total="page.allCount" :page-size="page.pageSize" @current-change="pageChange"></el-pagination>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import flatGreyHead from "@/components/header/flatGreyHead";
import navBar from "@/components/header/navBar";
import {
  addBlog,
  getBlogByTarget,
  getBlog,
  editBlog,
  delBlog
} from "@/api/blog";

import { addBlogType, getBlogTypes, delBlogType } from "@/api/blogType";

import tinymce from "@/components/Tinymce/index";

export default {
  components: {
    flatGreyHead,
    tinymce,
    navBar
  },

  created() {
    // this.reloadAllBlogs();
    this.reloadBlogs();
    this.reloadBlogTypes();
  },

  computed: {
    // 赏金是否显示
    rewardShow: function() {
      if (this.v.ei.blogType == "e2273f34-7b98-11e9-b364-f44d30a43073") {
        return false;
      } else {
        return true;
      }
    }
  },
  methods: {
    // 更改blog的类型
    changeBlogType(t){
      this.currentBlogType=t
      this.reloadBlogs()

    },
    // 添加blogtype方法集合
    addBlogType(v) {
      if (v == "show") {
        this.show.addBlogType = true;
      }

      // 如果是确定添加
      if (v == "confirm") {
        addBlogType(this.ed.addBlogType).then(r => {
          if (r.code == 200) {
            this.reloadBlogTypes();
            this.show.addBlogType = false;
          }
        });
      }
    },
    // 删除blog
    delBlog(id) {
      this.$confirm("真的要删除吗", "提示", {
        confirmButtonText: "想好了",
        cancelButtonText: "点错了"
      }).then(() => {
        delBlog({
          blogId: id
        }).then(() => {
          this.reloadBlogs();
        });
      });
    },
    // 编辑
    editBlog(id) {
      // 编辑前先根据blogid获取blog的详细内容

      getBlog({
        blogId: id
      }).then(r => {
        if (r.code == 200) {
          this.v.ei.blogType = r.data.blogType;
          this.v.ei.title = r.data.title;
          this.v.ei.reward = r.data.reward;
          this.v.ei.content = decodeURIComponent(r.data.content);
          this.show.createBlog = true;
          this.v.ei.id = r.data.id;
        }
      });
    },

    // 获取所有的blog类型
    reloadBlogTypes() {
      getBlogTypes().then(r => {
        if (r.code == 200) {
          this.list.blogTypes = r.data || [];
          if (this.list.blogTypes.length > 0) {
            this.v.ei.blogType = this.list.blogTypes[0];
          }
        }
      });
    },
    // 阅读blog
    readBlog(id) {
      // window.open("#/readBlog?id=" + id);
      this.$router.push({ path: "/readBlog?id=" + id });
    },
    // 分页改变
    pageChange(p) {
      this.page.pageIndex = p;
      this.reloadBlogs();
    },
    reloadBlogs(targetId) {
      // 刷新带分页数据
      getBlogByTarget({
        pageSize: this.page.pageSize,
        pageIndex: this.page.pageIndex,
        blogType: this.currentBlogType.id,
        targetId:targetId
      })
        .then(r => {
          if (r.code == 200) {
            this.list.blogs = r.data.content;            
            this.page.allCount=r.data.totalElements
          }
        })
        .catch();
    },
    dealReward() {
      (this.blog.reward = this.blog.reward || ""),
        (this.blog.reward = this.blog.reward.replace(/[^\d]/g, ""));
    },
    // 添加新blog
    addNewBlog() {
      // 编辑器内容编码
      this.v.ei.content = encodeURIComponent(this.v.ei.content);
      let para = this.v.ei;

      // 校验数字
      if (!parseInt(para.reward)) {
        // 悬赏格式不正确
        this.$message({
          message: "悬赏格式不正确",
          type: "error"
        });
        return false;
      }

      if (para.title == "") {
        this.$message({
          message: "标题太短了",
          type: "error"
        });
        return false;
      }

      if (para.content == "") {
        this.$message({
          message: "内容太少会被打~",
          type: "error"
        });
        return false;
      }

      // 如果有id是编辑
      if (para.id) {
        editBlog(para)
          .then(r => {
            if (r.code == 200)
              this.$message({ message: "编辑成功", type: "success" });
            this.show.createBlog = false;
            this.reloadBlogs();
          })
          .catch(e => {});
      } else {
        // 没有id是新建，添加targetId
        addBlog(para)
          .then(r => {
            if (r.code == 200)
              this.$message({ message: "添加成功", type: "success" });
            this.show.createBlog = false;
            this.reloadBlogs();
          })
          .catch(e => {});
      }
    },
    createBlog() {
      // 编辑器清空

      this.v.ei.title = "";
      this.v.ei.content = "";
      this.v.ei.blogType = "";
      this.v.ei.reward = 0;
      this.show.createBlog = true;
    },

    reloadAllBlogs() {
      listAllBlogs({})
        .then(r => {
          if (r.code == 200) this.list.blogs = r.data;
        })
        .catch();
    },
    //   添加新blog
    addBlog() {
      let blog = {
        // id: "",
        parentId: "sdadwad",
        content: "dawdawdasd",
        title: "ddddcccc"
      };
      addBlog(blog)
        .then()
        .catch();
    }
  },

  data() {
    const item = {
      date: "2016-05-02",
      name: "王小虎",
      address: "上海市普陀区金沙江路 1518 弄"
    };
    return {
      // 当前选中的blogtype
      currentBlogType: {
        id: "",
        title: ""
      },
      ed: {
        addBlogType: {
          title: "",
          id: ""
        }
      },
      tabPosition: "",
      tableData: Array(20).fill(item),
      v: {
        searchKeyword: "",
        ei: {
          blogType: "",
          title: "",
          content: "",
          reward: ""
        },

        // 表格属性：all/tucao/xuanshang/zhengji
        table_type: ""
      },
      blog: {
        content: "",
        visiable: "",
        title: ""
      },
      show: {
        createBlog: false,
        blogRewardInput: false,
        addBlogType: false
      },
      page: {
        pageSize: 10,
        pageIndex: 1,
        allCount: 0
      },
      list: {
        blogs: []
      }
    };
  }
};
</script>

<style>
.bloggou {
  font-size: 35px;
}
.bloggou:hover {
  color: red;
}

.el-header {
  background-color: #b3c0d1;
  color: #333;
  line-height: 60px;
}

.el-aside {
  color: #333;
}
</style>





